import React, { useState, Fragment } from 'react';
import classes from './CounterDesk.module.css';
import Button from '../components/Button/Button';

const CounterDesk = () => {
    const [counter, setCounter] = useState(0);
    const [ErrMsg] = useState('Counter Desk Contains Positive Values Only*');
    return (
        <Fragment>
            <div className={classes.CounterCard}>
                <h1>Counter Desk</h1>
                {
                    counter < 0 ?
                        <div className='text-center'>
                            <h5 className='my-4'>
                                <b>You clicked <strong>{() => setCounter(0)}</strong> times.</b>
                            </h5>
                            <p className='text-danger my-4 font-weight-bold'>{ErrMsg}</p>
                            <Button clicked={() => setCounter(counter + 1)}>
                                INCREASE
                            </Button>
                            <Button btnClass="Decrease">
                                DECREASE
                            </Button>
                            <Button clicked={() => setCounter(0)}>
                                RESET
                            </Button>
                        </div>
                        :
                        <div className='text-center'>
                            <h5 className=' my-5'>
                                <b>You clicked <strong>{counter}</strong> times.</b>
                            </h5>
                            <Button clicked={() => setCounter(counter + 1)}>
                                INCREASE
                            </Button>
                            <Button
                                btnClass="Decrease"
                                clicked={() => setCounter(counter - 1)}>
                                DECREASE
                            </Button>
                            <Button clicked={() => setCounter(0)}>
                                RESET
                            </Button>
                        </div>
                }
            </div>
        </Fragment>
    );
}

export default CounterDesk;