import React, { Fragment } from 'react';
import classes from './Button.module.css'

const Button = (props) => {
    return (
        <Fragment>
            <div>
                <button className={[classes.CustomButton , classes[props.btnClass]].join(' ')} onClick={props.clicked}>
                    <span><strong>{props.children}</strong></span>
                </button>
            </div>
        </Fragment>
    );
};

export default Button;